import { FileSystem } from 'spinal-core-connectorjs';

export function loadPtr<T extends spinal.Model>(
  loadPtrDictionary: Map<number, Promise<T>>,
  ptr: spinal.Ptr<T>
): Promise<T> {
  if (
    typeof ptr.data.value !== 'undefined' &&
    loadPtrDictionary.has(ptr.data.value)
  ) {
    return loadPtrDictionary.get(ptr.data.value);
  }

  if (typeof ptr.data.model !== 'undefined') {
    const res = Promise.resolve(ptr.data.model);
    if (ptr.data.value) {
      loadPtrDictionary.set(ptr.data.value, res);
    }
    return res;
  }

  if (typeof ptr.data.value !== 'undefined' && ptr.data.value === 0) {
    return Promise.reject('Load Ptr to 0');
  }

  if (typeof FileSystem._objects[ptr.data.value] !== 'undefined') {
    const res = Promise.resolve(<T>FileSystem._objects[ptr.data.value]);
    loadPtrDictionary.set(ptr.data.value, res);
    return Promise.resolve(res);
  }
  const res: Promise<T> = ptr.load();
  loadPtrDictionary.set(ptr.data.value, res);
  return res;
}
